import { useForm } from 'react-hook-form';
import { useNavigate } from 'react-router-dom';
import { ReactComponent as Logo } from '../../assets/logo.svg';
import Input from '../../components/Input/Input';
import css from './LoginPage.module.scss';

function LoginPage() {
    const {
        register,
        handleSubmit,
        formState: { errors },
    } = useForm({ mode: 'onBlur' });

    const navigate = useNavigate();
    const onSubmit = (data) => {
        console.log(data);
        navigate('/admin/order-list/');
    };

    return (
        <div className={css.container}>
            <div className={css.block}>
                <header className={css.block_header}>
                    <Logo />
                    <h2 className={css.block_header__title}>Need for drive</h2>
                </header>
                <form className={css.card} onSubmit={handleSubmit(onSubmit)}>
                    <h3 className={css.card_header}></h3>
                    <Input
                        label=""
                        type="text"
                        placeholder="    "
                        register={register}
                        required
                        minLength={{
                            value: 3,
                            message: '  3- ',
                        }}
                        errors={errors}
                    />
                    <Input
                        label=""
                        type="password"
                        placeholder=" "
                        register={register}
                        required
                        minLength={{
                            value: 3,
                            message: '  3- ',
                        }}
                        errors={errors}
                    />
                    <div className={css.card_footer}>
                        <button className={css.button} type="button">
                             
                        </button>
                        <button className={css.standart_button} type="submit">
                            
                        </button>
                    </div>
                </form>
            </div>
        </div>
    );
}

export default LoginPage;
